.modal {
    position: fixed;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
    background: rgba(0,0,0, 0.5);
    display: flex;
    justify-content: center;
    padding-top: 5rem;
    z-index: 99999;
}
.modal-body {
    padding: 2rem;
    width: 400px;
    border-radius: 5px;
    background: white;
    height: 200px;
}
button {
    margin-bottom: 5px;
}